import Component from './component';
import { TextVNode, ElementVNode } from './vnode'

export function createNode(tag: string, props: {}, childNodes: []): TextVNode | ElementVNode {
  if (arguments.length === 1) {
    return new TextVNode(arguments[0])
  }
  return new ElementVNode(tag, props, childNodes);
}

export function mount(selector: string, rootNode: Component) {
  let ele = document.querySelector(selector)
  let rootEle = rootNode.startRender().createEle()
  if (ele && rootEle) {
    if (ele.parentNode) {
      ele.parentNode.replaceChild(rootEle, ele)
    }
  }
}

export class Application {
  static depPool: any[] = []
}