import { Application } from "./application";
import { PropValue } from './value'
import { createNode } from './application'
import { ElementVNode, TextVNode } from "./vnode";

interface Config {
  data: {
    [key: string | symbol]: any
  }
}

abstract class Component {
  valueMap: Map<any, any>
  config: Config
  data?: {}

  constructor(config: Config) {
    // 响应式数据临时储存
    this.valueMap = new Map();
    this.config = config
    // 设置响应式数据对象
    this.setProxy()
  }

  setProxy() {
    let that = this;
    this.data = new Proxy(this.config.data || {}, {
      get(target, prop, receiver) {
        let valueTarget = that.valueMap.get(target)
        if (!(valueTarget && valueTarget[prop])) {
          if (!valueTarget) that.valueMap.set(target, {})
          that.valueMap.get(target)[prop] = new PropValue(target[prop])
        }
        let res = that.valueMap.get(target)[prop]
        for (let depArr of Application.depPool) {
          if (!depArr.includes(res)) {
            depArr.push(res)
          }
        }
        return res
      },
      set(target, prop, value, receiver) {
        let propValue = that.valueMap.get(target)[prop]
        propValue.realValue = value
        propValue.notify()
        return Reflect.set(target, prop, value, receiver)
      }
    })
  }

  abstract render(h: (tag: string, props: {}, childNodes: []) => TextVNode | ElementVNode): ElementVNode

  startRender() {
    return this.render(createNode)
  }
}

export default Component