import { Application } from "./application"
import Component from "./component"
import { Watcher, PropValue } from './value'

export class VNode {
  static ELEMENT: number = 0
  static TEXT: number = 1
}

export class TextVNode extends VNode {
  type: number = VNode.TEXT
  text: string | (() => string)
  textNode?: Text

  constructor(text: string) {
    super()
    this.text = text
    this.createTextNode()
  }

  createTextNode() {
    let vnodeValue = "";
    let depPropValue: PropValue[] = []
    if (typeof this.text === "function") {
      let pool = Application.depPool
      pool.push(depPropValue)
      vnodeValue = this.text()
      let index = pool.indexOf(depPropValue)
      pool.splice(index, 1)
    } else {
      vnodeValue = this.text
    }
    this.textNode = document.createTextNode(vnodeValue)
    if (typeof this.text === "function") {
      let cb = this.text
      for (let propValue of depPropValue) {
        let fn = () => {
          if (this.textNode) {
            this.textNode.nodeValue = cb.call(this)
          }
        }
        propValue.setDep(new Watcher(fn))
      }
    }
  }
}


export class ElementVNode extends VNode {
  static nativeEvents: { [key: string]: any } = {
    onclick: 'click',
  }

  tag: string
  props: {
    [key: string]: any
  }
  childNodes: Array<TextVNode | ElementVNode | Component>
  type: number = VNode.ELEMENT
  ele?: HTMLElement

  constructor(tag: string, props: {}, childNodes: []) {
    super()
    this.tag = tag;
    this.props = props;
    this.childNodes = childNodes;
  }

  createEle() {
    this.ele = document.createElement(this.tag)
    if (this.props) {
      for (let prop in this.props) {
        if (typeof this.props[prop] === 'function') {
          let event = ElementVNode.nativeEvents[prop];
          if (event) this.ele.addEventListener(event, this.props[prop].bind(this.ele))
          continue;
        }

        if (this.props[prop] === 'style') {

        }
      }
    }

    if (this.childNodes && this.childNodes.length) {
      for (let child of this.childNodes) {
        if (typeof child === "string") {
          let vnode = new TextVNode(child)
          if (vnode.textNode) this.ele.appendChild(vnode.textNode)
          continue
        }
        // 如果子组件是自定义组件
        if (child instanceof Component) {
          this.ele.appendChild(child.startRender().createEle())
          continue
        }

        if (child instanceof ElementVNode) {
          this.ele.appendChild(child.createEle())
        }

        if (child instanceof TextVNode) {
          if (child.textNode) {
            this.ele.appendChild(child.textNode)
          }
        }
      }
    }

    return this.ele
  }
}


